
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import InfiniteLoading from 'vue-infinite-loading'
import InlineLoading from '@/components/InlineLoading'
import { getSongs } from '@/api/'
export default {
directives: {
TransferDom
},
model: {
prop: 'visible',
event: 'setVisible'
},
props: ['visible'],
components: {
Checker,
CheckerItem,
CheckIcon,
PopupHeader,
XButton,
Popup,
InfiniteLoading,
InlineLoading
},
watch: {
visible (newVal) {
if (newVal) {
this.$nextTick(() => {
this.$refs.infiniteLoading.attemptLoad()
})
}
}
},
data () {
return {
songList: [],
songListValue: [],
time: 1,
infiniteLoading: null
}
},
methods: {
onClick (itemValue, itemDisabled) {
/* let song = this.songList.find(v => v.songid === itemValue)
if (song) {
let params = {
ht_id: this.$route.query.id,
song_id: song.songid,
name: song.songname,
author: song.author
}
addSong(params)
} */
},
infiniteHandler ($state) {
if (!this.infiniteLoading) {
this.infiniteLoading = $state
}
getSongs({ht_id: this.$route.query.id}).then((res) => {
if (Array.isArray(res.result)) {
this.songList = [...this.songList, ...res.result]
this.songListValue = this.songList.map(v => {
return v.song_id
})
}
$state.complete()
})
},
resetEvent () {
setTimeout(() => {
this.songList = []
this.songListValue = []
this.infiniteLoading && this.infiniteLoading.reset()
}, 1000)
}
}
}



